import { Service, IResult } from '../core';
import { ITenantLoginOptions } from './ITenantLoginOptions';

/**
 * @description
 * This service allows to get tenant login options.
 */
export class TenantLoginOptionsService extends Service<ITenantLoginOptions> {
  protected baseUrl = 'tenant';
  protected loginOptionsUrl = 'loginOptions';

  /**
   * Gets the tenant's login options.
   *
   * **Example**
   * ```typescript
   *
   *    (async () => {
   *      const {data, res} = await tenantLoginOptionsService.detail();
   *   })();
   * ```
   */
  async detail(): Promise<IResult<ITenantLoginOptions>> {
    let url = this.loginOptionsUrl;
    if (this.client.tenant) {
      url = `${this.loginOptionsUrl}?tenantId=${this.client.tenant}`;
    }
    const res = await this.fetch(url);
    const data = await res.json();
    return { res, data };
  }
}
