import { gettext } from '../gettext';

/**
 * A bulk operation can have one of the following general statuses.
 */
export enum OperationBulkGeneralStatus {
  SCHEDULED = gettext('SCHEDULED'),
  EXECUTING = gettext('EXECUTING'),
  EXECUTING_WITH_ERROR = gettext('EXECUTING_WITH_ERROR'),
  CANCELED = gettext('CANCELED'),
  FAILED = gettext('FAILED'),
  SUCCESSFUL = gettext('SUCCESSFUL'),
  DELETED = gettext('DELETED'),
  INCORRECT = gettext('INCORRECT')
}
