import { Service, IResult, IResultList, IFetchClient   } from '../core';
import { ISystemOption } from './ISystemOption';

export class SystemOptionsService extends Service<ISystemOption> {
  protected baseUrl = 'tenant/system';
  protected listUrl = 'options';
  protected propertyName = 'options';

  /**
   * Gets the details of given system option.
   *
   * @param {string|number|IIdentified} option System option object with mandantory fragments.
   *
   * @returns Response wrapped in [[IResult]]
   *
   * **Example**
   * ```typescript
   *
   *    const option: ISystemOption = {
   *      category: 'alarm.type.mapping',
   *      key: 'temp_to_high'
   *    };
   *
   *    (async () => {
   *      const {data, res} = await systemOptionsService.detail(option);
   *   })();
   * ```
   */
  async detail(option: ISystemOption): Promise<IResult<ISystemOption>> {
    return super.detail(option);
  }

  /**
   * Gets the list of system options filtered by parameters.
   *
   * @returns Response wrapped in [[IResultList]]
   *
   * @param {object} filter Object containing filters for querying system options.
   *
   * **Example**
   * ```typescript
   *
   *  const filter: object = {
   *     pageSize: 100,
   *     withTotalPages: true
   *   };
   *
   *   (async () => {
   *     const {data, res, paging} = await systemOptionsService.list(filter);
   *   })();
   * ```
   */
  async list(filter: object = {}): Promise<IResultList<ISystemOption>> {
    const headers = { 'content-type': 'application/json' };
    const url = this.listUrl;
    const res = await this.fetch(url, { headers, params: filter });
    const json = await res.json();
    const data = json[this.propertyName];

    return { res, data };
  }

  protected getDetailUrl(option: ISystemOption): string {
    return `${this.listUrl}/${option.category}/${option.key}`;
  }

  protected onBeforeCreate(obj: ISystemOption): any {
    return obj;
  }
}
