import { IAuthentication, ICredentials } from './IAuthentication';
import { FetchClient } from './FetchClient';

/**
 * Allows to use Cookies for Authorization to the
 * Cumulocity API.
 */
export class CookieAuth implements IAuthentication {
  user: string;
  private logoutUrl = 'user/logout';

  updateCredentials({user}: ICredentials = {}): string {
    this.user = user;
    return undefined;
  }

  getFetchOptions(options) {
    const xsrfToken = this.getCookieValue('XSRF-TOKEN');
    const headers: any = { 'X-XSRF-TOKEN':  xsrfToken};
    options.headers = Object.assign(headers, options.headers);
    return options;
  }

  getCometdHandshake(config: {ext?: {}} = {}) {
    const KEY = 'com.cumulocity.authn';
    const xsrfToken = this.getCookieValue('XSRF-TOKEN');
    const ext = config.ext = config.ext || {};
    ext[KEY] = Object.assign(ext[KEY] || {}, { xsrfToken });
    return config;
  }

  async logout() {
    if (this.user) {
      delete this.user;
    }
    const client = new FetchClient();
    client.setAuth(this);
    const method = 'POST';
    const body = JSON.stringify({});
    const headers = { 'content-type': 'application/json', accept: 'application/json' };
    return client.fetch(this.logoutUrl, { headers, body, method });
  }

  private getCookieValue(name) {
    const value = document.cookie.match('(^|;)\\s*' + name + '\\s*=\\s*([^;]+)');
    return value ? value.pop() : '';
  }
}
