import { IAuthentication, ICredentials } from './IAuthentication';
import { btoa } from 'b2a';

// this is var and not const to please typedoc https://github.com/TypeStrong/typedoc/issues/691
var secrets = new WeakMap(); // tslint:disable-line

/**
 * Allows to use Basic-Auth for Authorization to the
 * Cumulocity API.
 */
export class BasicAuth implements IAuthentication {

  user: string;
  /**
   * Authenticates the given user against the given tenant.
   * @param name
   * @param password
   * @param tenant
   */
  constructor(credentials?: ICredentials) {
    this.updateCredentials(credentials);
  }

  updateCredentials({ tenant, user, password, token, tfa }: ICredentials = {}): string {
    const secret = secrets.get(this) || {};
    if (user && tenant) { user = `${tenant}/${user}`; }
    user = user || this.user;
    password = password || secret.password;
    if (!token && user && password) { token = btoa(`${user}:${password}`); }
    if (user) { this.user = user; }
    token = token || secret.token;
    tfa = tfa || secret.tfa;
    secrets.set(this, { tfa, token, password });
    return token;
  }

  getFetchOptions(options) {
    const secret = secrets.get(this);
    const { token, tfa } = secret;
    const xsrfToken = this.getCookieValue('XSRF-TOKEN');
    const headers: any = {
      Authorization: `Basic ${token || ''}`,
      ...(xsrfToken ? { 'X-XSRF-TOKEN': xsrfToken } : undefined)
    };

    if (tfa) {
      headers.tfatoken = tfa;
    }
    options.headers = Object.assign(headers, options.headers);
    return options;
  }

  getCometdHandshake(config: { ext?: {} } = {}) {
    const secret = secrets.get(this);
    const { token, tfa } = secret;
    const KEY = 'com.cumulocity.authn';
    const ext = config.ext = config.ext || {};
    const auth = ext[KEY] = Object.assign(ext[KEY] || {}, { token, tfa });
    return config;
  }

  logout() {
    delete this.user;
    secrets.set(this, {});
  }

  private getCookieValue(name) {
    try {
      const value = document.cookie.match('(^|;)\\s*' + name + '\\s*=\\s*([^;]+)');
      return value ? value.pop() : undefined;
    } catch (ex) {
      return undefined;
    }
  }
}
