import fetch from 'cross-fetch';
import { Headers, Request, RequestInit, Response } from 'cross-fetch';

import { IFetchClient } from './IFetchClient';
import { IFetchOptions } from './IFetchOptions';
import { IFetchResponse } from './IFetchResponse';
import { IAuthentication } from './IAuthentication';

let auths = new WeakMap(); // tslint:disable-line

export class FetchClient implements IFetchClient {
  tenant: string = '';
  defaultHeaders = {};
  constructor(
    authOrBaseUrl?: IAuthentication | string,
    public baseUrl?: string,
  ) {
    if (typeof authOrBaseUrl === 'string') {
      baseUrl = authOrBaseUrl;
    } else {
      this.setAuth(authOrBaseUrl);
    }
    this.baseUrl = this.resolveServerUrl(baseUrl);
  }

  setAuth(auth: IAuthentication) {
    auths.set(this, auth);
  }

  async fetch(url: string, init?: IFetchOptions): Promise<IFetchResponse> {
    let fetchFn = fetch;
    try { fetchFn = window.fetch || fetchFn; } catch (e) { /* do nothing */ }
    const fullUrl: string = this.getUrl(url, init);
    const options: RequestInit = this.getFetchOptions(init);
    return fetchFn(fullUrl, options);
  }

  getUrl(url: string = '', options?: IFetchOptions): string {
    const params = options && options.params;
    let paramPart = '';
    if (params && Object.keys(params).length) {
      paramPart = Object.keys(params)
        .map((k) => {
          let vals = params[k];
          const encodedKey = encodeURIComponent(k);
          if (!Array.isArray(vals)) {
            vals = [vals];
          }
          return vals.map((v) => `${encodedKey}=${encodeURIComponent(v)}`).join('&');
        })
        .join('&');
      paramPart = `?${paramPart}`;
    }
    const baseUrl = this.baseUrl.replace(/\/+$/, '').replace(/^\/+/, '');
    const partialUrl = url.replace(/\/+$/, '').replace(/^\/+/, '');
    return `${baseUrl}/${partialUrl}${paramPart}`;
  }

  getFetchOptions(options: IFetchOptions = {}): RequestInit {
    const auth = auths.get(this);
    options.headers = Object.assign({}, this.defaultHeaders, options.headers, { UseXBasic: true });
    delete options.params;
    options = auth ? auth.getFetchOptions(options) : options;
    return options;
  }

  getCometdHandshake(config: any = {}) {
    const auth = auths.get(this);
    return auth ? auth.getCometdHandshake(config) : config;
  }

  private resolveServerUrl(baseUrl: string = ''): any {
    if (baseUrl && baseUrl.startsWith('http')) {
      return baseUrl.replace(/\/+$/, '');
    }
    try {
      const location = window.location;
      return `${location.protocol}//${location.host}/${baseUrl.replace(/\/+$/, '')}`;
    } catch (ex) {
      throw Error('Your environment does not support relative URLs. Please provide a base URL.');
    }
  }
}
