import {
  Service,
  IResult,
  IResultList,
  IFetchClient,
  IFetchOptions,
  IFetchResponse
} from '../core';
import { IExternalIdentity } from './IExternalIdentity';

/**
 * This class allwos for managing external identifiers.
 */
export class IdentityService {
  private baseUrl: string = 'identity';
  private propertyName: string = 'externalIds';
  private client: IFetchClient;

  constructor(client: IFetchClient) {
    this.client = client;
  }

  /**
   * Gets the list of identities filtered by parameters.
   *
   * @returns Response wrapped in [[IResultList]]
   *
   * @param {object} filter Object containing filters for querying identity.
   *
   * **Example**
   * ```typescript
   *
   *  const filter: object = {
   *     pageSize: 100,
   *     withTotalPages: true
   *   };
   *
   *   (async () => {
   *     const {data, res, paging} = await identityService.list(filter);
   *   })();
   * ```
   */
  async list(managedObjectId: string): Promise<IResultList<IExternalIdentity>> {
    const headers = { accept: 'application/json' };
    const url = this.getExternalIdsOfGlobalIdUrl(managedObjectId);
    const res = await this.fetch(url, { headers });
    const json = await res.json();
    const data = json[this.propertyName];
    return { res, data };
  }

  /**
   * Creates a new identity.
   *
   * @param {IExternalIdentity} identity Identity object with mandantory fragments.
   *
   * @returns Response wrapped in [[IResult]]
   *
   * **Example**
   * ```typescript
   *   const linkedManagedObjectId = '123';
   *   const identity: IExternalIdentity = {
   *      type: 'type',
   *      externalId: '1',
   *      managedObject: {
   *        id: linkedManagedObjectId
   *      }
   *    };
   *
   *    (async () => {
   *      const {data, res} = await identityService.create(identity);
   *   })();
   * ```
   */
  async create(identity: IExternalIdentity): Promise<IResult<IExternalIdentity>> {
    const headers = { 'content-type': 'application/json', accept: 'application/json' };
    const method = 'POST';
    const body = JSON.stringify(identity);
    const url = this.getExternalIdsOfGlobalIdUrl(identity.managedObject.id);
    const res = await this.fetch(url, { headers, method, body });
    const data = await res.json();
    return { res, data };
  }

  /**
   * Gets the details of an identity.
   *
   * @param {IExternalIdentity} identity Identity object with mandantory fragments.
   *
   * @returns Response wrapped in [[IResult]]
   *
   * **Example**
   * ```typescript
   *
   *    const identity: IExternalIdentity = {
   *      type: 'type',
   *      externalId: '1'
   *    };
   *
   *    (async () => {
   *      const {data, res} = await identityService.detail(identity);
   *   })();
   * ```
   */
  async detail(identity: IExternalIdentity): Promise<IResult<IExternalIdentity>> {
    const headers = { accept: 'application/json' };
    const url = this.getExternalIdUrl(identity);
    const res = await this.fetch(url, { headers });
    const data = await res.json();
    return { res, data };
  }

  /**
   * Removes an identity with given id.
   *
   * @returns Response wrapped in [[IResult]]
   *
   * @param {IExternalIdentity} identity Identity object with mandantory fragments.
   *
   * **Example**
   * ```typescript
   *
   *   const identity: IExternalIdentity = {
   *      type: 'type',
   *      externalId: '1'
   *    };
   *
   *    (async () => {
   *      const {data, res} = await identityService.delete(identity);
   *   })();
   * ```
   */
  async delete(identity: IExternalIdentity): Promise<IResult<IExternalIdentity>> {
    const headers = { accept: 'application/json' };
    const method = 'DELETE';
    const url = this.getExternalIdUrl(identity);
    const res = await this.fetch(url, { headers, method });
    return { res, data: null };
  }

  private async fetch(url: string, init?: IFetchOptions): Promise<IFetchResponse> {
    const res: IFetchResponse = await this.client.fetch(url, init);
    if (res.status >= 400) {
      let data = null;
      try {
        data = await res.json();
      } catch (ex) {
        try {
          data = await res.text();
        } catch (ex) {
          // do nothing
        }
      }
      throw { res, data };
    }
    return res;
  }

  private getExternalIdsOfGlobalIdUrl(managedObjectId: string | number): string {
    return `/${this.baseUrl}/globalIds/${managedObjectId}/externalIds`;
  }

  private getExternalIdUrl(identity: IExternalIdentity): string {
    return `/${this.baseUrl}/externalIds/${identity.type}/${identity.externalId}`;
  }
}
