/**
 * A specific audit record can have one of the following types
 */
export enum AuditRecordType {
  ALARM = 'Alarm',
  BULK_OPERATION = 'BulkOperation',
  CEP_MODULE = 'CepModule',
  EVENT = 'Event',
  INVENTORY = 'Inventory',
  INVENTORY_ROLE = 'InventoryRole',
  OPERATION = 'Operation',
  OPTION = 'Option',
  GROUP = 'Group',
  SMART_RULE = 'SmartRule',
  SYSTEM = 'SYSTEM',
  TENANT = 'Tenant',
  USER = 'User',
}
